    // 
    /*
     
     hi =cm, wi = kg
     
     backData.bmi        //BMI
     backData.loWeight   //
     backData.hiWeight   //
     backData.status     //
     backData.report	    //
     */
    weightHandle : function(hi,wi){
      //
      var heigh = (parseFloat(hi)/100).toFixed(2);  // M
      var weight = parseFloat(wi);  //KG
      //
      var bmiDa = this.computeBMI(heigh,weight);  //BMI
      var standerDa = this.standardWeight(heigh); //
      var reportDa = this.weightReport(bmiDa);    //
      //
      var backData = new Object();
      backData.bmi = bmiDa;                     //BMI
      backData.loWeight = standerDa.loWeight;   //
      backData.hiWeight = standerDa.hiWeight;   //
      backData.status = reportDa.status;        //
      backData.report = reportDa.report;		  //
      return backData;

      /*
       var showString = "";
       showString += "BMI="+bmiDa+"\n\r";
       showString += "loWeight="+standerDa.loWeight+", hiWeight="+standerDa.hiWeight+"\n\r";
       showString += "report="+reportDa.report+", status="+reportDa.status +"\n\r";

       alert(showString);
       */
    },


    //BMI  hig wei   BMI =  (KG) / M * M
    computeBMI: function(hig,wei){
      //
      var dataBMI = ( wei / (hig * hig) ).toFixed(2);
      return dataBMI;
    },

    //  (KG) = BMI * M * M
    standardWeight: function(heigh){
      // var heigh = (parseFloat(hig)/100).toFixed(2);  //M
      //BMI
      var loBMI = 18.5;
      var hiBMI = 24;
      //
      var lo = ( loBMI * heigh * heigh ).toFixed(2);  //
      var hi = ( hiBMI * heigh * heigh ).toFixed(2);  //
      //
      var back = new Object();
      back.loWeight = lo;
      back.hiWeight = hi;
      return back;
    },

    //BMI
    weightReport: function(val){
      // var value = parseFloat(val);
      var value = val;
      //
      var backString = "";
      var status = "";
      //
      var count1 = 18.5;
      var count2 = 24;
      var count3 = 27;
      //
      if(value < count1){
        backString = "";
        status = "";
      }else if(count1 <= value && value < count2){
        backString = "";
        status = "";
      }else if(count2 <= value && value <= count3){
        backString = "";
        status = "";
      }else if(value > count3){
        backString = "";
        status = "";
      }
      //
      var backObject = new Object();
      backObject.report = backString;
      backObject.status = status;
      return backObject;
    },

