    //    valo: vahi
	bloodPressureReport: function(valo,vahi){

      var loObject = this.bloodSugar_lo_Report(valo);
      var hiObject = this.bloodSugar_hi_Report(vahi);

      var backString = "";
      var status = "";

      if( loObject.statusVal == 1 && hiObject.statusVal == 1 ){
      	backString = "";
        status = "";
      }else{
      	backString = loObject.status + "/" + hiObject.status + ""
      	+ "";
        status = "";
      }

      var backObject = new Object();
      backObject.report = backString;
      backObject.status = status;
      backObject.lo_status = loObject.statusVal;
      backObject.hi_status = hiObject.statusVal;

      return backObject;
    },

