    // 
    heartRateReport:function (val){
      var lev = 0;
      console.log(val+"###")
      if(val.toString().indexOf("%")>0){
        lev = parseFloat(val.replace('%',''));
      }else{
        lev = parseFloat(val);
      }

      var backString = "";
      var status = "";

      if(lev < 60){
        backString = "40/";
        status = "";
      }else if(lev >= 60 && lev <= 100){
        backString = "";
        status = "";
      }else if(lev > 100){
        backString = "";
        status = "";
      }

      var backObject = new Object();
      backObject.report = backString;
      backObject.status = status;

      return backObject;
    },


