    //
    /*
      / 
     */
    ruReport:function (val){

      var item = 0;

      //
      for(var key in val){
        if(key == 'urine_PH'){
          item = this.urlsReport(val[key], 5 , 8.5).outType;
        }else if(key == 'urine_specific_gravity'){
          item = this.urlsReport(val[key], 1 , 1.03).outType;
        }else{
          item = this.urlsReport(val[key], -1 , 7).outType;
        }
      }

      var backString = "";
      var status = "";
      var types = 0;

      if(item > 0){
        backString = "";
        status = "";
        types = 1;
      }else{
        backString = "";
        status = "";
        types = 0;
      }

      var backObject = new Object();
      backObject.report = backString;
      backObject.status = status;
      backObject.outType = types;

      return backObject;
    },

