      val = val.toString();
      if(val.indexOf("<") >= 0){
        subVal = val.substring(val.indexOf("<")+1,val.length);  //
        chageVal = parseFloat(subVal) - 0.1;                    // -0.1
        val = chageVal+"";
      }else if(val.indexOf(">") >= 0){
        subVal = val.substring(val.indexOf(">")+1,val.length);  //
        chageVal = parseFloat(subVal) + 0.1;                    // +0.1
        val = chageVal+"";
      }

      // 
